.dx-widget-report-panel{
    .btn-group>.dropdown-menu {
        margin-top: 10px;
    }

    .dx-widget-report-filter-panel{
        margin-left: 10px;
    }

    .dx-widget-report-period{
        margin-bottom: 15px;
    }
    
    .dx-widget-report-chart div.xAxis div.tickLabel 
    {    
        transform: rotate(-90deg);
        -ms-transform:rotate(-90deg); /* IE 9 */
        -moz-transform:rotate(-90deg); /* Firefox */
        -webkit-transform:rotate(-90deg); /* Safari and Chrome */dx-widget-report-period
        -o-transform:rotate(-90deg); /* Opera */
        /*rotation-point:50% 50%;*/ /* CSS3 */
        /*rotation:270deg;*/ /* CSS3 */
        margin-top: 20px;
    }

    .dx-widget-report-chart{
        margin-bottom: 20px;
    }
    
    .dx-widget-report-scrollable-menu {
        height: auto;
        max-height: 300px;
        overflow-x: hidden;
    }
}


    


.dx-widget-report-chart-tooltip{
    position: absolute;
    display: none;
    border: 1px solid rgb(51, 51, 51); 
    padding: 4px; 
    color: rgb(255, 255, 255); 
    border-radius: 3px;
    background-color: rgb(51, 51, 51); 
    opacity: 0.8;
}
